import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {ProfileService} from '../../services/profile/profile.service';
import {EditProfilePage} from '../edit-profile/edit-profile.page';
import { ToastController, ModalController,AlertController } from '@ionic/angular';
import * as moment from 'moment';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.page.html',
    styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
    
    public profile:any;
    public loading:boolean;
    public user:any;
    public rating:Array<number>;
    
    constructor(private authenticationService:AuthenticationService, private route:ActivatedRoute, 
        private profileService: ProfileService, public modalController: ModalController, private alertCtrl: AlertController) { 

        this.loading = true;
        
        this.user = {};
    
    
    }

    ngOnInit() {
    }
    
    ionViewDidEnter(){
        this.authenticationService.getUserData().subscribe((data) => {

            this.user = data;
            
        });          
    }
    
    public getProfile(){
        this.profileService.getProfile(this.profile.user_id).then((data) => {
            this.profile = data;
            this.loading = false;
        }).catch(() => {
            this.loading = false
        })
    }    
    
    public async editProfile(){
        let profile = Object.assign({}, this.profile);
        profile.email = this.user.email;
        const modal = await this.modalController.create({
          component: EditProfilePage,
          componentProps: {profile:profile}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    this.profile = data.data.profile;
                    this.profileService.updateProfile(this.profile).then(() => {
                        
                    })            

                } 
            });        
        
        
        
        await modal.present();         
    } 

    
    public formatDate(date){
        return moment(date).format("DD/MM/YYYY");
    }      
    
  
    
    
}
